import React from 'react';
import Carousel from 'react-bootstrap/Carousel'


import slider1bg from "../assets/Banners/1banner.png"
import slider2bg from "../assets/Banners/2banner.png"
import slider3bg from "../assets/Banners/DicebgBanner.jpg"



function Slider() {
  return (
    <div className="SliderCarrouselContainer">
    <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100 imgSlide"
          src={slider1bg}
          alt="First slide"
        />
        <div className="firstSlideText1">
          <h3>Celebrating our 8th Aniversary!</h3>
            <br/> All personalized dice bags
          <div style={{color: 'red', fontSize: '40px'}}>55% OFF</div>
          <button id='slide1Button'>LEARN MORE</button>
        </div>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100 imgSlide"
          src={slider2bg}
          alt="Second slide"
        />

        <div className="slider2Text">
           LIMITED EDITION<br/><br/>
           Handcrafted Kits<br/>
           Includes bags, dices and cups for your best rolls! <br/><br/>
           <button id='slide2Button'>LEARN MORE</button>
        </div>

      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100 imgSlide"
          src={slider3bg}
          alt="Third slide"
        />

        <div className='slider3Text'>
        More than 500 Styles and colors!
        </div>
      </Carousel.Item>
    </Carousel>
  </div>
  )
}

export default Slider;