import React from 'react'


function Catalog(props) {

    function RenderItems(){

        let ItemsRender = props.catalogItems.map((item)=>{
            return(
            <span className="catalogItem" key={item.id}>
                <img className="imgItem" height="120" src={item.src} alt="itemImage"></img>
                <p className="Item1">{item.name}</p>
                <p className="price">{item.price}</p>
                <div className="buttonBuy">BUY NOW</div>
            </span>
            )
        })
   
        return ItemsRender
    }

    let renderedItems = RenderItems()

    return(
 <div id='catalog'>
    {renderedItems}
 </div>
 )
}


export default Catalog;