import React from 'react'
import facebookIcon from "../assets/Catalog Assets/social_media/facebook.png"
import InstaIcon from "../assets/Catalog Assets/social_media/INSTAICON.png"
import YTICON from  "../assets/Catalog Assets/social_media/YTICON.jpg"



function Footer() {

    return (
        <div id="endPage">
          <div id='socialMedia'>
            <div className="socialMediaIcon"><img className='iconBottom' src={facebookIcon} width="30px" alt='Facebook'/>
              Facebook Page</div>
            <div className="socialMediaIcon"><img className='iconBottom' src={InstaIcon} width="30px" alt='Instagram'/>
              Instagram
            </div>
            <div className="socialMediaIcon"><img className='iconBottom' src={YTICON} width="30px" alt='YTICON'/>
                Youtube Channel</div>
          </div>
          <div id='infoList'>
            <span>FAQ</span>
            <span>PARTNERSHIPS</span>
            <span>CUSTOMER SERVICE</span>
            <span>AWARDS</span>
            <span></span>
          </div>
          <div id='ContactInfo'>
            <span>Location: Somewhere in Vancouver</span>
            <span>Cellphone: (99)9999-9999</span>
            <span style={{cursor: 'pointer'}}>Work with us</span>
          </div>
        </div>

    )
}


export default Footer;