import React from 'react';
import globalIcon from '../assets/Catalog Assets/icons/GlobalIcon.png'
import creditCard from '../assets/Catalog Assets/icons/CreditCard.png'
import compassIcon from '../assets/Catalog Assets/icons/Compass.png'

function InfoBar2() {
    return (
        <div id='advantages'>
        <span className="advantagesItem"><img className='iconBottom' src={globalIcon} alt='worldwide'/> WorldWide Delivery</span>
        <span className="advantagesItem"><img className='iconBottom' src={creditCard} alt='CreditCard'/> 6x Credit Card </span>
        <span className="advantagesItem"><img className='iconBottom' src={compassIcon} alt='Compass'/>Find Us </span>
        <span className="advantagesItem">Return Policy</span>
      </div>        
    )
}

export default InfoBar2;