import React from 'react';
import logo from '../assets/Banners/FKSPNG.png'
import searchIcon from '../assets/Catalog Assets/icons/Search.png'
import CartIcon from '../assets/Catalog Assets/icons/cart.png'
import MobileMenuIcon from "../assets/Catalog Assets/icons/Menu.png"


function Banner() {
    return (
        <div id='banner'>

            <div id='firstBannerHalf'>
                <span className='bannerOpt'>bags</span>
                <span className='bannerOpt'>dices</span>
                <span className='bannerOpt'>custom cases</span>
                <span className='bannerOpt'>About Us</span>
                <span></span>
            </div>
            <span id='menuMobile'><img src={MobileMenuIcon} alt='MobleMenu'/></span>
            <span id='logoSpan'><img src={logo} className="logo" alt='logo'/></span>
                    <div id='secondBannerHalf'>
                        <span></span>
                        <span className='bannerOpt'>Contact us</span>
                        <span className='bannerOpt'>Our Values</span>
                        <span className='bannerOpt'><img src={searchIcon} alt='search'/></span> 
                        <span className='bannerOpt'><img src={CartIcon} alt='CartIcon'/></span> 
                    </div>
        </div>
    )
}

export default Banner;